* {
  padding: 0;
  margin:0;
  font-size: 10px;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
button {
  border: 1px solid red ;
  padding: 0;
  width: 50px;
  height: 50px;
  margin: 0;
  position:relative
  
}
.board {
  /* margin: auto; */
  display: grid;
  grid-template-columns: repeat(6,auto);
  justify-content: center;
  
   
  
  
  margin-top: 20px;
  padding: 0;
  grid-row-gap: 0;
  grid-column-gap: 0;
 
}
.status {
  text-align: center;
  margin-top: 40px;
}
.row > div {
  display: inline;
}
button:hover {
  background-color: rgba(255,255,255,0.4);
}
#test {
  position: relative;
  top: 100px;
  left: 100px;
}

